.action {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    font-family: fat, sans-serif;
    margin: 0.25em;

    width: 15vw;
    min-width: 200px;


    z-index: 1;
}

.action .box {
    background-color: violet;
    background-image: linear-gradient(110deg, blueviolet 40%, rgba(238, 130, 238, 0) 60%);
    transition: background-color 0.5s;

    width: 100%;
    padding: 0.2em;
    /*border-radius: 1em;*/


    z-index: 1;

    font-size: 2em;
}

.info {
    visibility: hidden;
    width: 0;
    padding-left: 0.2em;


    z-index: 1;
}


.info p {
    margin-top: 0;
    position: absolute;
    background-color: white;
    width: 15vw;
}


.disabled {
    opacity: 0.5;
}

.enabled:hover {
    box-shadow: 0 0 0.5em #824182;
}

.enabled:active {
    background-color: #9c4d9c;
}

.action:hover .info {
    visibility: visible;
}

.action .box .cost {
    position: relative;
    float: right;
}

.action .box .title {
    float: left;
}